import { API } from '../constants'

const services = {
  list: API + '/employee',
  delete: API + '/employee',
  register: API + '/employee',
}

const _token = localStorage.getItem('__token')

async function doGetAll() {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(services.list, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

async function doGetActives() {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.list}?onlyactives=true`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

async function doGetById(id: any) {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.list}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

async function doDelete(id: any) {
  const requestOptions = {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.delete}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

async function doUpdate(body: any, id: number) {
  const requestOptions = {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(`${services.register}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

async function doRegister(body: any) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(services.register, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export default {
  getAll: doGetAll,
  getActives: doGetActives,
  getById: doGetById,
  delete: doDelete,
  update: doUpdate,
  register: doRegister,
}
